import {  
    mapState,
	mapMutations  
} from 'vuex';  
export default {
	data() {
		return {
			remarks:'',
		}
	},
	computed: {
		...mapState(['hasLogin','userInfo'])
	},
	methods: {
		...mapMutations(["login"]),
		inputChange(e){
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		save(){
			//验证这些参数
			let self =this;
			if(self.remarks==''){
				self.$api.msg('昵称不能为空');
				return;
			}
			uni.showLoading({
				title:"正在提交"
			})
			self.$axios(self.$baseUrl.profile,{avatar:self.userInfo.portrait,
			nickname:self.remarks,
			gender:self.userInfo.gender,
			birthday:self.userInfo.birthday,
			}).then(res =>{
				uni.hideLoading()
				self.userInfo.nickname=self.remarks;
				self.login(self.userInfo);
				uni.navigateBack({
					animationDuration:300
				})
				if(res.data.data.code==1){
					
					uni.showToast({title:'昵称修改成功！',icon:'success'});
				}else{
					self.$api.msg(res.data.msg);
				}
			},(error) =>{
				uni.hideLoading()
				console.log(error)
			})
		}
		
	}
}
